import React from 'react'
import { Row, Col } from 'reactstrap'

import Ccube from '../../Ccube'
const PrivacyTerms = (props) => {
  return (
    <React.Fragment>
      <h2 className="warning spacing nodisplay">Privacy Terms</h2>
      <Row>
        <Col lg="8" md="6" sm="12">
          <Row className="match-height">
            <Col md="12">Privacy</Col>
          </Row>
          <Row className="match-height">
            <Col md="12">Terms and Conditions</Col>
          </Row>
        </Col>
        <Ccube />
      </Row>
    </React.Fragment>
  )
}

export default PrivacyTerms
